$primary-color: #222222;
$secondary-color: #393E41;
$tertiary-color: #FF2277;
$primary-text-color: #FDFFFC;
$secondary-text-color: #393E41;
$tertiary-text-color: #FF2277;
$title-font: Monospace;
$body-font: Arial, Helvetica, sans-serif;
$header-height: 100px;
$search-height: 50px;
$thumbnail-size: 300px;
$bottom-height: 90px;
$regular-font-size: 20px;

* {
  margin: 0;
}

// /* clears the 'X' from Internet Explorer */
// input[type=search]::-ms-clear {  display: none; width : 0; height: 0; }
// input[type=search]::-ms-reveal {  display: none; width : 0; height: 0; }

// /* clears the 'X' from Chrome */
// input[type="search"]::-webkit-search-decoration,
// input[type="search"]::-webkit-search-cancel-button,
// input[type="search"]::-webkit-search-results-button,
// input[type="search"]::-webkit-search-results-decoration { display: none; }

input[type="search"]::-webkit-search-cancel-button{
    //-webkit-appearance: none;
    display: none;
}

input[type="search"]::-ms-clear{
    display: none;
}

body {
    background-color:$primary-color;
    color:$primary-text-color;
    font-family: $body-font;
    height: 100vh;
    width: 100vw;;
    // overflow: hidden;
}

a {
    color:white;
}

a:hover {
    text-shadow: 0 0 5px $primary-text-color;
}

code {
    background-color: $secondary-color;
    padding: 3px;
    border-radius: 5px;
}

.header {
    font-family: $title-font;
    background-color: $secondary-color;
    width: 100%;
    height: $header-height;
}

.header ::slotted {
    float:left;
}

.header img {
    height: ($header-height - 10);
    margin: 5px;
}

.main-body{

    top: $header-height;
    height: calc(100% - #{$header-height});
}

.navigator {
    background-color: $tertiary-color;
    height: $search-height;
    top: $header-height;
    width: 100%;
    margin:0;


}

.navigator img {
    display: block;
    height: $search-height - 10;
    margin: 5px;
    right: 5px;
    pointer-events:none;
    position: absolute;
}

.navigator input {
    -moz-box-shadow:    inset 0 0 10px $secondary-color;
    -webkit-box-shadow: inset 0 0 10px $secondary-color;
    box-shadow:         inset 0 0 10px $secondary-color;
    font-size: $search-height - 20;
    background:$tertiary-color;
    border:none;
    color:$primary-text-color;
    width: 100%;
    height:100%;
    padding: 10px;
    padding-right:$search-height;
}

.navigator input::placeholder {
    color:$primary-text-color;
}

.project-list {
    margin:0;
    height: calc(100vh - #{$search-height} - #{$header-height} - #{$bottom-height});
    text-align: center;
    overflow: auto;
}

.project-thumbnail {
    text-decoration:none;
    display: inline-block;
    height: $thumbnail-size;
    width: $thumbnail-size;
    margin: 30px;
    background-size:contain;
    background-repeat:no-repeat;
    box-shadow:inset 0 0 $thumbnail-size 100px $primary-color, 10px 10px 1px 0px $tertiary-color;
    border-radius: $thumbnail-size/10;
}

.project-thumbnail:hover {
    box-shadow:inset 0 0 $thumbnail-size 100px $primary-color,10px 10px 1px 0px $secondary-color;
}

.project-thumbnail div {
    margin-top:100px;
}

.project-page {
    margin:0;
    height: calc(100vh - #{$header-height} - #{$bottom-height});
    text-align: center;
    overflow: hidden;
}

.project-page h1:first-of-type{
    padding-top: 10px;
}

.project-page p:first-of-type{
    padding: 10px;
}

.close-icon {
    height: 40px;
    margin: 5px;
    position: absolute;
    right: 0;
}

.footer{
    height: $bottom-height;
    width: 100vw;
    position: fixed;
    bottom: 0;
    text-align: center;
    padding: 20px;
    margin:0;
    background-color: $secondary-color;
}

.footer hr{
    border-color:$tertiary-color
}

.footer button, .footer a{
    font-size:$regular-font-size;
    padding: 0;
    color: $primary-text-color;
    text-decoration: underline;
    background: none;
    border: none;
}

.footer button:hover, .footer a:hover {
    text-shadow: 0 0 5px $primary-text-color;
}

.copyright {
    margin-top: 20px;
    color:$tertiary-text-color;
    font-size: $regular-font-size;
}

@media only screen and (min-width: 768px){
    .header h1, img {
        float: left;
        font-size: $header-height - 20;
    }

    .header h1 {
        padding: 0;
        bottom: 5px;

    }

    .project-list {
        padding: 5px 200px;
    }

    .footer button, .footer a {
        display: inline;
        margin: 5px 15px;
    }
}

@media only screen and (max-width: 768px){
    .header {
        text-align: center;
    }

    .header h1{
        font-size: 30px;
        margin: 20px 10px;
        float: right;
    }

    .header img {
        float: left;
    }

    .project-thumbnail{
        margin: 10px;
    }

    .footer button, .footer a {
        display: block;
        margin: 10px auto;
    }

}

